    --softBlack: rgba(0,0,0,0.5);
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    min-height: 100vh;
    color: white;
    display: grid;
    grid-template-rows: 1fr 2fr 1fr;
    /* justify-content: center; */
    background-color: black;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    font-family: 'urbanist';
    font-size: 1.5em;
}

/* CLASSES */

.button {
    cursor: pointer;
}

.icon {
    filter: invert(100%) sepia(0%) saturate(18%) hue-rotate(314deg) brightness(106%) contrast(104%);
}

.scaled {
    scale: 2.5;
}

/* #HEADER */

#header, #footer {
    background-color: var(--softBlack);
    align-items: center;
}

#header {
    display: inline-grid;
    grid-template-columns: 1fr 2fr 1fr;
}

#home {
    position: relative;
}

#home .button {
    scale: 2.5;
    position: absolute;
    left: 100px;
    top: -20px;
    border: 1px solid white;
}

#title {
    justify-self: center;
    justify-content: center;
}

#navbar {
    display: flex;
    justify-content: flex-end;
}

#navbar :last-child {
    margin-left: 30px;
    margin-right: 60px;
}

/* #CONTENT */

#content {
    display: flex;
    justify-content: center;
}

#modules {
    display: grid;
    justify-items: center;
    width: 85%;
    height: 80%;
    background-color: rgba(255, 217, 0, 0.25);
    padding: 30px;
    margin: 50px;
}

#modules p {
    padding: 0 100px;
    background-color: var(--softBlack);
}

/* #FOOTER */

#footer {
    display: flex;
    justify-content: space-between;
}

#socials p {
    display: flex;
    gap: 20px;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,4BAA4B;AAChC;;AAEA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;AAC1B;;AAEA;IACI,iBAAiB;IACjB,YAAY;IACZ,aAAa;IACb,+BAA+B;IAC/B,6BAA6B;IAC7B,uBAAuB;IACvB,yDAAqD;IACrD,uBAAuB;IACvB,gBAAgB;AACpB;;AAEA,YAAY;;AAEZ;IACI,eAAe;AACnB;;AAEA;IACI,+FAA+F;AACnG;;AAEA;IACI,UAAU;AACd;;AAEA,YAAY;;AAEZ;IACI,kCAAkC;IAClC,mBAAmB;AACvB;;AAEA;IACI,oBAAoB;IACpB,kCAAkC;AACtC;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,UAAU;IACV,kBAAkB;IAClB,WAAW;IACX,UAAU;IACV,uBAAuB;AAC3B;;AAEA;IACI,oBAAoB;IACpB,uBAAuB;AAC3B;;AAEA;IACI,aAAa;IACb,yBAAyB;AAC7B;;AAEA;IACI,iBAAiB;IACjB,kBAAkB;AACtB;;AAEA,aAAa;;AAEb;IACI,aAAa;IACb,uBAAuB;AAC3B;;AAEA;IACI,aAAa;IACb,qBAAqB;IACrB,UAAU;IACV,WAAW;IACX,yCAAyC;IACzC,aAAa;IACb,YAAY;AAChB;;AAEA;IACI,gBAAgB;IAChB,kCAAkC;AACtC;;AAEA,YAAY;;AAEZ;IACI,aAAa;IACb,8BAA8B;AAClC;;AAEA;IACI,aAAa;IACb,SAAS;AACb","sourcesContent":[":root {\n    --softBlack: rgba(0,0,0,0.5);\n}\n\n* {\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n}\n\nbody {\n    min-height: 100vh;\n    color: white;\n    display: grid;\n    grid-template-rows: 1fr 2fr 1fr;\n    /* justify-content: center; */\n    background-color: black;\n    background-image: url(./assets/pagoda-background.jpg);\n    font-family: 'urbanist';\n    font-size: 1.5em;\n}\n\n/* CLASSES */\n\n.button {\n    cursor: pointer;\n}\n\n.icon {\n    filter: invert(100%) sepia(0%) saturate(18%) hue-rotate(314deg) brightness(106%) contrast(104%);\n}\n\n.scaled {\n    scale: 2.5;\n}\n\n/* #HEADER */\n\n#header, #footer {\n    background-color: var(--softBlack);\n    align-items: center;\n}\n\n#header {\n    display: inline-grid;\n    grid-template-columns: 1fr 2fr 1fr;\n}\n\n#home {\n    position: relative;\n}\n\n#home .button {\n    scale: 2.5;\n    position: absolute;\n    left: 100px;\n    top: -20px;\n    border: 1px solid white;\n}\n\n#title {\n    justify-self: center;\n    justify-content: center;\n}\n\n#navbar {\n    display: flex;\n    justify-conten